import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { Http, Response } from '@angular/http'
import 'rxjs/add/operator/map'

@Component({
  selector: 'app-location-code',
  templateUrl: './location-code.component.html',
  styleUrls: ['./location-code.component.css']
})
export class LocationCodeComponent  {
  @Output() childEventgetPackage = new EventEmitter();
  @Output() childEventshowCart = new EventEmitter();
  @Output() childEventShowGridView = new EventEmitter();

  someProperty;
  records;
  latitude = "";
  longitude = "";
  CartItemCount;

  constructor(private http: Http) {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(position => {
        this.latitude = position.coords.latitude.toString()
        this.longitude = position.coords.longitude.toString()
        this.getContacts('https://maps.googleapis.com/maps/api/geocode/json?key=AIzaSyC_xl6rnJXCIXbnkbBBtK6tN6kaIDdUh4c&latlng=' + position.coords.latitude.toString() + ',' + position.coords.longitude.toString());
        this.getData('https://maps.googleapis.com/maps/api/geocode/json?key=AIzaSyC_xl6rnJXCIXbnkbBBtK6tN6kaIDdUh4c&latlng=' + position.coords.latitude.toString() + ',' + position.coords.longitude.toString());
      });
    }
  }
  postalCode: any;
  getData(apiURL) {
    return this.http.get(apiURL)
      .map((res: Response) => res.json())
  }
  getContacts(apiURL) {
    let data1 = [];
    this.getData(apiURL).subscribe(data => {
      console.log(data.results[0].address_components.length);
      console.log(data.results[0].address_components[data.results[0].address_components.length - 1].short_name);
      console.log(data.results[0].address_components["postal_code"]);
      if (this.postalCode)
      {
        return;
      }
      this.postalCode = data.results[0].address_components[data.results[0].address_components.length - 1].short_name
      console.log(this.postalCode);
      this.someProperty = data.results[0].formatted_address;
      for (var i = 0; i < data.results.length; i++) {
        var adress = data.results[i].formatted_address;
        //    console.log(adress);
        data1.push(data.results[i].formatted_address);
      }
      //this.records = data1;


    })
    this.records = data1;
  }

  myEvent(event) {
    console.log(this.someProperty);
    //this.someProperty = "text changes";
  }


  updateCart(CartItemCount)
  {
    this.CartItemCount = CartItemCount;
    if (this.CartItemCount==0)
      this.childEventShowGridView.emit();
  }
  ShowCartItems(){
    this.childEventshowCart.emit();
  }
  ShowGridView()
  {
    this.childEventShowGridView.emit();
  }
  
  updateplace()
  {
    this.postalCode = (document.getElementById("location") as HTMLInputElement).value;
  }
}



