import { Component, ElementRef, AfterViewInit, NgZone  } from '@angular/core';
import { GoogleSignInSuccess } from 'angular-google-signin';
declare const gapi: any;

@Component({
  selector: 'app-login-google',
  templateUrl: './login-google.component.html',
  styleUrls: ['./login-google.component.css']
})
export class LoginGoogleComponent  {
  
  title1 = "Google SignIn button";
  angularVersion1 = 'latest stable';
  googleLoginButtonId = "google-login-button";
  constructor(private element: ElementRef, private _zone: NgZone) {
    //console.clear();
    console.log('ElementRef: ', this.element);
  }
  ngAfterViewInit() {
    console.log('calling AppComponent this.googleInit() 1');
    gapi.signin2.render(
      this.googleLoginButtonId,
      {
        "onSuccess": this.onGoogleLoginSuccess,
        "scope": "profile",
        "theme": "dark",

      });
    //this.googleInit();
    console.log('calling AppComponent this.googleInit() Closed 1');
  }
  private myClientId: string = '420515554338-b6nfpjj5bg8jtojgf02ep09j850qsupb.apps.googleusercontent.com';

  onGoogleLoginSuccess = (loggedInUser) => {
    console.log("this._zone.run1");
    this.ngAfterViewInit()
    this._zone.run(() => {
      this.userAuthToken = loggedInUser.getAuthResponse().id_token;
      this.userDisplayName = loggedInUser.getBasicProfile().getName();
      this.title1 = loggedInUser.getBasicProfile().getName();
      console.log(loggedInUser.getBasicProfile().getName());
    });
    console.log(this.userDisplayName);

  }
  userAuthToken: any;
  userDisplayName: any;

  ngOnInit() {
  }
  
  
}



